.jumbotron {
  margin-bottom: 0;
  background-color: #6ed2ff;
  color: white;
}

.navbar-default {
  background-color: #fff;
  border: none;
}

.navbar-brand {
  padding-bottom: 35px;
}

.footer {
  margin-top: 20px;
}

.footer-container {
  text-align: center;
  font-weight: bold;
  color: white;
  font-size: 1.1em;
  padding-top: 20px;
  padding-bottom: 20px;
  background-image: url("../resources/images/brickWallModified.png");
  background-repeat: repeat;
}

.wide-content {
  background-image: url("../resources/images/liquidWaterTop.png");
  -webkit-animation: backgroundScroll 20s linear infinite;
  animation: backgroundScroll 20s linear infinite;
}

@-webkit-keyframes backgroundScroll {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -400px 0;
  }
}

@keyframes backgroundScroll {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -400px 0;
  }
}

html {
  overflow-y: scroll;
}

body {
  min-height: 400px;
}

.body-container {
  margin-bottom: 100px;
}

h2 {
  margin-bottom: 50px;
}

